body {
  margin: 0;
  font-family: "Inter", sans-serif;
  background: #0f0f0f;
  color: #fff;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.header {
  background: #111;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header h1 {
  font-size: 1.5rem;
  color: #0ff;
}

.header nav a {
  margin-left: 1rem;
  color: #ccc;
  text-decoration: none;
}

.hero {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 4rem 1rem;
}

.hero h2 {
  font-size: 2.5rem;
  color: #0ff;
}

.hero p {
  font-size: 1.2rem;
  color: #aaa;
  margin-top: 1rem;
}

.footer {
  background: #111;
  padding: 1rem;
  text-align: center;
  font-size: 0.9rem;
  color: #555;
}
